.card {
  border-radius: 2px;
  height: 160px;
  margin: 1rem;
  position: relative;
  width: 180px;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background: #fff9ee;
  padding-top: 10px;
}
.card > .img-container > img {
  width: 100%
}

.card > .img-container:hover {
  background:#f5e2b7;
}




